    /*
     *function call when any change occurs in select list.it has configurableSelectedObject hold item object and key
     */
    ProductPage.prototype.onChangeConfigurableAttribute = function (configurableSelectedObject, key) {
        var _this = this;
        if (!configurableSelectedObject) {
            return;
        }
        var count = 0;
        var total = 0;
        var flag = 0;
        this.configPrice = [];
        this.selectedList = [];
        //mapping between select list
        forEach(this.productData.body.associated_products.attributes, function (allConfigData, allConfigKey) {
            if (typeof (allConfigData.vertualKey) == 'object') {
                ++flag;
                count++;
                _this.configPrice.push({ price: allConfigData.vertualKey.price }); //hold  price of selected item
                _this.selectedList.push(allConfigData);
                if (allConfigData.label == "color") {
                    setTimeout(function () {
                        var myDiv = document.getElementById('color'); //change icon color of configurable callor filed
                        myDiv.style.color = ((allConfigData.vertualKey.label).trim()).replace(" ", "") || ""; //remove space from lable
                    }, 100);
                }
            }
            if (key != allConfigKey) {
                forEach(allConfigData.options, function (allConfigValue) {
                    allConfigValue.shown = false;
                    forEach(configurableSelectedObject.products, function (currVal) {
                        forEach(allConfigValue.products, function (allConfigProductsVal) {
                            if (currVal == allConfigProductsVal) {
                                allConfigValue.shown = true;
                            }
                        });
                    });
                });
            }
            else {
                forEach(allConfigData.options, function (allConfigValue) {
                    if (flag == 1) {
                        allConfigValue.shown = true;
                    }
                });
            }
        });
        forEach(this.configPrice, function (value) {
            total += (parseFloat(value.price));
        });
        this.diffrentTypeProductData(total);
        this.selectshow = false;
        //disable button when select list is not checked
        if (this.keys.length == count) {
            if (this.customDisable == false) {
                this.disable = false;
            }
            this.config = false;
        }
        else {
            if (this.customDisable == false) {
                this.disable = true;
            }
            this.config = true;
        }
        this.configurabilData();
    };
